* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 0;
  position: fixed;
  background-color: white;
  width: 100vw;
  padding-bottom: 13px; }

.menu {
  margin-top: 10px; }

.menu ul {
  display: flex;
  list-style: none; }

.logo {
  width: 154.5px;
  padding-top: 10px; }

.link {
  font-family: "Rubik", sans-serif;
  text-decoration: none;
  color: #4a4a4a;
  padding: 10px 10px 13px 10px;
  text-transform: uppercase; }

.link:hover {
  color: #14d9c4;
  text-decoration: underline;
  transition-delay: 1s; }

.main {
  height: 100vh;
  width: 100vw;
  margin-top: 112px;
  margin-bottom: 90px; }

.hero {
  height: 100vh;
  width: 100vw;
  background-image: url(/assets/images/fondo.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  /*background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;*/
  display: flex; }

.hero-title {
  padding-left: 20px;
  font-size: 30px;
  color: white;
  font-family: "Rubik", sans-serif;
  font-weight: bold;
  width: min-content;
  letter-spacing: normal;
  text-transform: capitalize;
  display: flex;
  align-items: center; }

.footer {
  background-color: #252525;
  height: 90px;
  width: 100vw;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.footer-copy {
  font-family: "Rubik", sans-serif;
  color: #676a6d; }
